@import "country-dropdown.assets.scss";

$alias: 'WooZone-AMZReport-';

##{$alias}wrapper {
  font-family: 'Roboto', sans-serif;
  float: left;
  width: 100%;

  .#{$alias}not-sing-in {
    background-color: #fff;
    padding: 20px;

    > div {
      display: grid;
      grid-template-columns: 392px 1fr;
      margin-bottom: 30px;

      .#{$alias}sign-btn {
        padding: 7px 15px;
        background-color: #FF6F00;
        color: #fff;
        font-size: 12px;
        text-transform: uppercase;
        border-radius: 4px;
      }
    }
  }

  .#{$alias}get-reports {
    button {
      border: none;
      background-color: #43a047;
      color: #fff;
      font-size: 12px;
      font-weight: bold;
      border-radius: 5px;
      text-transform: uppercase;
      float: left;
      display: inline-block;
      padding: 12px 20px;

      &:hover {
        background-color: #8bc24a;
      }
    }
  }

   .#{$alias}top {
      display: grid;

      .#{$alias}last-update {
        text-align: right;
        font-size: 14px;
        color: #BDBDBD;
      }

      .#{$alias}custom-range {
        border: 1px solid #BDBDBD;
        padding: 30px;
        margin-top: 10px;
        position: absolute;
        z-index: 999;
        background-color: #fff;

        a.#{$alias}close {
          position: absolute;
          top: 10px;
          right: 10px;
          background-color: #e53935;
          text-decoration: none;
          color: #fff;
          width: 25px;
          height: 24px;
          font-size: 13px;
          text-align: center;

          &:hover {
            background-color: #b71c1c;
          }

          span {
            font-size: 20px;
            position: relative;
            top: 2px;
          }
        }

        p {
          margin-top: 0;
        }

        .DayPicker-wrapper {
          padding-bottom: 0;
        }

        .DayPicker-Month:first-child {
          padding-left: 0;
          margin-left: 0;
        }
      }
    }

    .#{$alias}interval-wrapper {
      .#{$alias}select {
        font-size: 13px;
        background: #212121;
        color: #fff;
        display: inline-block;
        padding: 12px 10px 12px 15px;
        cursor: pointer;
        border-radius: 5px;
        user-select: none;

        &:hover {
          background: #BDBDBD
        }

        span {
          position: relative;
          margin-top: -2px;
          margin-left: 4px;
        }
      }

      .#{$alias}interval {
        border: 1px solid #BDBDBD;
        padding: 30px;
        position: absolute;
        background-color: #fff;
        z-index: 99;

        li {
          padding-left: 20px;
          position: relative;
          cursor: pointer;
          user-select: none;
          margin-bottom: 12px;

          &:last-child {
            margin-bottom: 0;
          }

          &:before {
            display: block;
            content: "";
            width: 7px;
            height: 7px;
            position: absolute;
            left: 4px;
            top: 4px;
            background: #FF5722;
            border-radius: 50%;
            opacity: 0;
          }

          &.on:before {
            opacity: 0.8;
          }

          &:hover:before {
            opacity: 1;
          }

          &:after {
            position: absolute;
            top: 0;
            left: 0;
            content: "";
            display: block;
            border: 1px solid #616161;
            width: 15px;
            height: 15px;
            border-radius: 50%;
          }
        }
      }
    }

    ul:not(.#{$alias}interval) {
      border-top: 1px solid #EEEEEE;
      display: block;
      margin: 0;
      padding: 0;
      display: grid;
      grid-template-columns: repeat( 6, 1fr );
      grid-gap: 20px;
      margin-top: 20px;

      li {
        border-right: 1px solid #EEEEEE;
        padding: 20px;

        &:last-child {
          border: none;
        }

        span:first-child {
          display: block;
          font-size: 13px;
          font-weight: bold;
          margin-bottom: 10px;
        }
      }
    }

  .#{$alias}chart {
    padding: 20px;
    background: #fff;
    border-radius: 5px;
    margin-bottom: 20px;

    h2 {
      margin: 0;
      padding: 0;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      margin-bottom: 20px;
    }
  }

  .#{$alias}app-loading {
    font-size: 13px;
    font-weight: bold;
    color: #fff;
  }

  .#{$alias}aff-ids {
    background-color: #fff;
    margin-bottom: 40px;
    padding: 20px;
    border-radius: 5px;

    &:after {
      content: "";
      display: table;
      clear: both;
    }

    .#{$alias}select-country,
    .#{$alias}get-current-id,
    .#{$alias}top,
    .#{$alias}get-reports {
      float: left;
      margin-right: 20px;
    }

    .#{$alias}get-current-id {
      background: #F5F5F5;
      padding: 12px 20px;
      font-weight: bold;
      border-radius: 5px;
    }
  }

   .tableone tr.__is_header,
   .tableone tr:nth-child(3) td,
   .tableone tr:nth-child(4) td  {
       background-color: #ceecaa !important;
    }


  .#{$alias}table {
    padding: 20px;
    background: #fff;
    border-radius: 5px;
    margin-bottom: 20px;

    table {
      width: 100%;

      thead {
        td {
          background-color: #fff;
          padding: 10px 12px;
          font-weight: bold;
        }
      }

      tr td {
        border: 1px solid #fafafa;
        padding: 5px 12px;

        &:nth-child(1) {
          font-family: monospace;
        }
      }


      tr:nth-child(odd) td {
        background-color: #f2f2f2;
      }

      tr.__is_header {
        td {
          background-color: #f2f2f2;

          &:first-child {
            padding-left: 20px !important;
            font-weight: bold;
          }
        }
      }
    }

    &.#{$alias}lop {
      table tr {
        td:first-child {
          padding-left: 30px;
        }
      }
    }
  }

  .lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 30px;
    margin-top: 5px;
  }
  .lds-ellipsis div {
    position: absolute;
    top: 10px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #FF5722;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .lds-ellipsis div:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
  }
  @keyframes lds-ellipsis1 {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  @keyframes lds-ellipsis3 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
  }
  @keyframes lds-ellipsis2 {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(24px, 0);
    }
  }

  .#{$alias}get-google-chrome {
    text-align: center;
    padding: 60px;

    h1 {
      font-family: 'Roboto', sans-serif;
      font-size: 25px;
      line-height: 25px;
      margin-bottom: 10px;
      padding-top: 15px;
      padding-bottom: 25px;
      display: block;
    }

    img {
      width: 96px;
    }

    a._is_btn {
      background-color: #174ea6;
      color: #fff;
      font-size: 12px;
      padding: 9px 20px;

      &:hover {
        background: darken(#174ea6, 10%);
        color: #fff;
      }
    }
  }

  .#{$alias}get-extension {
    position: relative;
    text-align: center;
    font-weight: bold;
    background-color: #fff;
    padding: 20px;
    overflow: hidden;
    border-radius: 5px;

    .#{$alias}loader {
      background: none;
      position: relative;
      display: inline-block;

      .lds-roller {
        position: relative;
        top: 0;
        left: 0;
        margin-left: 0;

        div:after {
          background: #2675b9;
        }
      }
    }

    .#{$alias}get-extension-block {
      width: 320px;
      display: inline-block;
      display: grid;
      grid-template-columns: 80px 1fr;
      grid-gap: 20px;
      margin: 0;
      margin-top: 30px;
      margin-bottom: 10px;
      text-align: left;
      background: #F5F5F5;
      border: 1px solid #E0E0E0;
      padding: 10px;
      border-radius: 4px;

      img {
        width: 80px;
        height: auto;
        background: #fff;
        border-radius: 4px;
      }

      h4 {
        margin-top: 5px;
        font-size: 18px;
      }

      a._is_btn {
        background-color: #174ea6;
        color: #fff;
        font-size: 12px;
        padding: 9px 20px;
        border-radius: 2px;

        &:hover {
          background: darken(#174ea6, 10%);
          color: #fff;
        }
      }
    }
  }

  .#{$alias}validate-left {
    display: block;
    width: 49%;
    float: left;
    p {
      font-size: 15px;
      text-align: left;
    }
    .#{$alias}info-block {
      font-size: 14px;
      background-color: #174ea6;
      color: #fff;
      border-radius: 2px;
      padding: 5px 10px;
    }
    .#{$alias}info-text {
      font-size: 12px;
      color: #7b7b7b;
      font-style: italic;
    }
  }

  .#{$alias}validate-right {
    overflow: hidden;
    display: block;
    float: right;
    width: 49%;
    img {
      width: 100%;
    }
  }

  .#{$alias}validate-extension {
    overflow: hidden;
    padding: 20px;
    border-radius: 5px;
    background-color: #fff;

    h2 {
      margin-top: 0;
    }

    form {
      width: 450px;
      margin-top: 20px;

      input {
        width: 100%;
        padding: 10px;
        font-size: 13px;
        margin-bottom: 10px;
      }

      button {
        margin-top: 0px;
        width: 100%;
        background: #305b9b;
        border: none;
        padding: 12px;
        font-size: 13px;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        text-transform: uppercase;

        &:hover {
          background: #3e3d78;
        }

        span {
          font-size: 20px;
          color: #FDD835;
          margin-right: 5px;
        }
      }
    }
  }

  .#{$alias}loader {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 999;
    background: rgba( 0,0,0, 0.5 );

    .lds-roller {
      display: inline-block;
      position: relative;
      width: 64px;
      height: 64px;
      position: absolute;
      top: 20%;
      left: 50%;
      margin-left: -32px;
      div {
        animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        transform-origin: 32px 32px;
        &:after {
          content: " ";
          display: block;
          position: absolute;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background: #fff;
          margin: -3px 0 0 -3px;
        }
        &:nth-child(1) {
          animation-delay: -0.036s;
          &:after {
            top: 50px;
            left: 50px;
          }
        }
        &:nth-child(2) {
          animation-delay: -0.072s;
          &:after {
            top: 54px;
            left: 45px;
          }
        }
        &:nth-child(3) {
          animation-delay: -0.108s;
          &:after {
            top: 57px;
            left: 39px;
          }
        }
        &:nth-child(4) {
          animation-delay: -0.144s;
          &:after {
            top: 58px;
            left: 32px;
          }
        }
        &:nth-child(5) {
          animation-delay: -0.18s;
          &:after {
            top: 57px;
            left: 25px;
          }
        }
        &:nth-child(6) {
          animation-delay: -0.216s;
          &:after {
            top: 54px;
            left: 19px;
          }
        }
        &:nth-child(7) {
          animation-delay: -0.252s;
          &:after {
            top: 50px;
            left: 14px;
          }
        }
        &:nth-child(8) {
          animation-delay: -0.288s;
          &:after {
            top: 45px;
            left: 10px;
          }
        }
      }
    }

    @keyframes lds-roller {
      0% {
        transform: rotate(0deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }
  }
}
