/* Variables - Custom Country JavaScript  */
$alias: "AZON-country-list-";
$country_width: 180px;

.#{$alias}wrapper {
	font-family: "Roboto";
	position: relative;
	user-select: none;

	.#{$alias}default {
		border: 1px solid #6995dc;
		width: $country_width;
		padding: 12px 27px 13px 14px;
		background: #ffffff;
		display: inline-block;
		line-height: 15px;
		position: relative;
		cursor: pointer;
		border-radius: 4px;
		font-size: 14px;
		font-family: "Roboto";
		font-weight: bold;
		color: #1e1e1e;

		&:hover {
			background: #315898;
			color: #fff;

			&:after {
				color: #fff;
			}
		}

		&:after {
			font-family: dashicons;
			content: "\f347";
			display: block;
			position: absolute;
			top: 50%;
			right: 15px;
			transform: translateY(-50%);
			color: #315898;
			font-size: 13px;
		}

		span {
			display: inline-block;
			font-size: 13px;
			vertical-align: middle;

		}
		img {
			height: 14px;
			display: inline-block;
			margin-right: 10px;
			vertical-align: middle;
		}
	}

	ul {
		display: block;
		width: $country_width;
		background: #fff;
		position: absolute;
		top: 40px;
		left: 0;
		border: 1px solid #e8e9ed;
		z-index: 99;
		display: none;

		li {
			list-style: none;
			padding: 12px 15px !important;
			line-height: 10px;
			margin-bottom: 0;
			cursor: pointer;

			&:hover {
				background: #315898;
				color: #fff;
			}

			span {
				display: inline-block;
				font-size: 13px;
				vertical-align: middle;

			}
			img {
				height: 14px;
				display: inline-block;
				margin-right: 10px;
				vertical-align: middle;
			}
		}
	}
}
